<template>
  <div id='app' class="hello">
   <div class="">  
    <h1>{{ msg }}</h1>
   </div> 

    <div class="container">      
        <div class="row padding-block">
           <div class="col-lg-4 offset-lg-4">
       
          <autocomplete :search="search"
           placeholder="Pesquise por crypto moedas"
           aria-label="Pesquise por crypto moedas"          
           :get-result-value="getResultValue" 
            @submit="handleSubmit"
            auto-select />

           </div>
        </div> 
        <div class="row padding-block">  
           <div class="col-lg-8 offset-lg-1" v-if="resultCoin" >
              <div class="table-responsive">
               <table class="table table-stripped table-hover ">
                  <thead class="">
                     <tr>
                        <th>Sigla</th>
                        <th>Nome</th>
                        <th>Ranking</th>
                        <th>Preço</th>
                        <th>Base</th>
                     </tr>    
                  </thead>
                  <tbody>
                       <tr>
                         <td>{{resultCoin.symbol}}</td>
                         <td>{{resultCoin.name}}</td>
                         <td>{{resultCoin.rank}}</td>
                         <td>{{resultCoin.quotes.USD.price | formatCurrency}}</td>   
                         <td><i class="fa fa-dollar-sign" > USD</i></td>
                       </tr>
                  </tbody>
               </table>                
              </div> 
           </div>
        </div>    
    </div>


 
    
   <router-view  />
  


  </div>
</template>

<script>

import Autocomplete from '@trevoreyre/autocomplete-vue'
import { asyncLoading } from 'vuejs-loading-plugin'

export default {
  name: 'CryptoCoinComponent',
  props: {
    msg: String
  },
  data:()=>({
    test: [],
    resultCoin: null,
    crypto: {
      id: 0,
      symbol: '',
      websit_slug: ''
    }
  }),
  components: {
    Autocomplete
  },
  methods:{
    loadData(config)
    {      
        axios(config)
         .then(res => {
             this.test = res.data.data
           }).catch(err => console.log(err))
            
    },
     loadCoin(config)
    {      
       
       let result =  axios(config)
         .then(res =>  res.data)
            .catch(err => console.log(err))
          this.$loading(true)
           asyncLoading(result).then((res)=>{
                this.resultCoin =  res
                this.msg = res.name
           }).catch(()=>{})
            
    },
    search(input) {
      if (input.length < 1) { return [] }
      return this.test.filter(el => {
        return el.website_slug.toLowerCase()
          .startsWith(input.toLowerCase())
      })
    },
    getResultValue(result) {
      return result.name
    },    
    handleSubmit(result) {  
     
      this.loadCoin({url: `/crypto/${result.id}`})
    }
  },
  beforeMount(){
    
  },
  created() {
      this.loadData({url: '/crypto'})
  }
}
</script>

